<template>
  <el-container>
    <!--顶部-->
    <el-header style="border-bottom: 1px solid rgb(128,128,128);">
      <el-row style="margin: 10px 15px">
        <el-col :span="1">
          <!--收缩条-->
          <a href="#" @click="changeCollapse" style="font-size: 25px;color:#909399;"><i
              :class="collpaseIcon"></i></a>
        </el-col>
      </el-row>
      <div>
          <span >ORC口算识别系统-管理员端</span>
      </div>
      <div class="opt-wrapper">
        <el-dropdown :hide-on-click="false">
          <div class="demo-basic--circle">
            <div class="block">
              <el-avatar :size="40" :src="avatar" :class="['avatar-info']">
              </el-avatar>
            </div>
          </div>
          <!-- 头像下拉列表 -->
          <el-dropdown-menu slot="dropdown" style="margin-top:-30px;margin-left: -40px;">
            <el-dropdown-item><i class="el-icon-info"></i>个人中心</el-dropdown-item>
            <el-dropdown-item @click.native="logout"><i class="el-icon-switch-button"></i>退出登录</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>
    </el-header>
    <!--中央区域-->
    <el-main>
      <el-container>
        <!--左侧导航栏-->
        <el-aside :style="{width:collpaseWidth}">
          <!--default-active代表导航栏默认选中哪个index, :collapse决定导航栏是否展开，为boolean类型
          :router决定导航栏是否开启路由模式，即在菜单item上设置路由是否生效，值为boolean类型-->

          <el-menu
              default-active="0"
              class="el-menu-vertical-demo"
              :collapse="isCollapse"
              :router="true"
          >
            <!--index设置当前item的下标，:route则是传一个对象进行，指定路由-->
            <el-menu-item index="0" :route="{path:'/admin'}">
              <i class="fa fa-magic"></i>
              <span slot="title"> 首页</span>
            </el-menu-item>
            <el-submenu index="1">
              <template slot="title">
                <i class="fa fa-cogs"></i><span> 用户管理</span>
              </template>
              <el-menu-item index="/admin/users/1"><i class="fa fa-cog"></i> 用户设置
              </el-menu-item>
            </el-submenu>
            <el-submenu index="2">
              <template slot="title">
                <i class="fa fa-users"></i>
                <span> 数据统计</span>
              </template>

              <el-menu-item index="/admin/ansys/1"><i class="fa fa-address-card-o"></i>
                数据统计1
              </el-menu-item>
              <el-menu-item index="/admin/ansys/2"><i class="fa fa-envelope-o"></i> 数据统计2</el-menu-item>
            </el-submenu>
            <el-submenu index="3">
              <template slot="title">
                <i class="fa fa-users"></i>
                <span> 模型训练</span>
              </template>
              <el-menu-item index="/admin/model"><i class="fa fa-envelope-o"></i> 模型训练1</el-menu-item>
            </el-submenu>
          </el-menu>

        </el-aside>
        <!--主内容显示区域-->
        <el-main>
          <!--路由渲染-->
          <router-view></router-view>
        </el-main>
      </el-container>
    </el-main>
    <!--底部-->
    <el-footer style="border-top: 1px solid rgb(128,128,128)"></el-footer>
  </el-container>
</template>

<script>
// 这一大段JS就是为了做收缩/展开导航栏而用的！
export default {

  name: "Main",
  data: function () {
    return {
      isCollapse: false, // 决定左侧导航栏是否展开
      avatar: require('../assets/CodePath.jpg')
    }
  },
  computed: {
    collpaseIcon: function () { // 左侧导航栏是否展开状态的图标
      // 如果是展开状态就图标向右，否则图标向左
      return this.isCollapse ? 'el-icon-s-fold' : 'el-icon-s-unfold';
    },
    collpaseWidth: function () { // 左侧导航栏是否展开状态的宽度
      // 如果是展开状态就导航栏宽度为65px，否则200px
      return this.isCollapse ? '65px' : '200px';
    }
  },
  methods: {
    changeCollapse: function () { // 更改左侧导航栏展示状态
      this.isCollapse = !this.isCollapse;
    },
    logout() {
      this.$axios.post('/logout', this.loginForm).then(res => {
        console.log(res)
        //清理数据
        this.$store.commit('resetState');
        //跳转到登录路由
        this.$router.push({path: "/login"})
      });
    }
  },
}
</script>

<style scoped>
/*整体显示区域布局样式*/
.el-container {
  height: 100%;
}

.el-header, .el-main {
  padding: 0;
}

/*左边导航栏具体样式*/
.el-menu-vertical-demo.el-menu {
  padding-left: 20px;
  text-align: left;
  height: 100%;
  padding: 0;
}

el-container > .el-menu-vertical-demo.el-menu {
  padding: 0;
}

.el-submenu .el-menu-item, .el-menu-item {
  min-width: 50px;
}

.el-menu-item {
  padding: 0;
}

header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: #fff;
}


/*设定头像图片样式*/
.avatar-info {
  margin-top: 10px;
  margin-right: 40px;
  cursor: pointer;
}
</style>
